import Layout from "../components/Layout";
import React, {useState} from 'react';
import styled from "styled-components";
import {TagsSection} from "./Moneys/TagsSection";
import {NoteSection} from "./Moneys/NoteSection";
import {CategorySection} from "./Moneys/CategorySection";
import {NumberPadSection} from "./Moneys/NumberPadSection";
import {useRecords} from "../hooks/useRecords";

const MyLayout = styled(Layout)`
  display:flex;
  flex-direction: column;
`;

const CategoryWrapper = styled.div`
    background:#c4c4c4;
`;

export type CategoryType = '-' | '+'

const defaultFormData = {
  tagIds: [] as number[],
  note: '',
  category: '-' as CategoryType,
  amount: 0
};

function Money() {
  const [selected, setSelected] = useState(defaultFormData);

  const {addRecord} = useRecords();

  const onChange = (obj: Partial<typeof selected>) => {
    setSelected({...selected, ...obj});
  };

  const submit = () => {
    if (selected.amount > 0 && selected.tagIds.length >= 1) {
      addRecord(selected);
      window.alert('记账成功！ (•̀ᴗ•́)و ̑̑');
      setSelected(defaultFormData);
    } else if (selected.tagIds.length < 1) {
      window.alert('必须选择一个标签！ (๑°ㅁ°๑)‼');
      return;
    } else if (selected.amount <= 0) {
      window.alert('不能记 0 块钱的账！ (σ｀д′)σ');
      return;
    }
  };
  return (
    <MyLayout scrollTop={9999}>
      <TagsSection value={selected.tagIds}
                   onChange={(tagIds) => onChange({tagIds: tagIds})}>
      </TagsSection>

      <NoteSection value={selected.note}
                   onChange={(note) => onChange({note: note})}>
      </NoteSection>

      <CategoryWrapper>
        <CategorySection value={selected.category}
                         onChange={(category) => onChange({category: category})}>
        </CategorySection>
      </CategoryWrapper>

      <NumberPadSection value={selected.amount}
                        onChange={(amount) => onChange({amount: amount})}
                        onOk={submit}
      >
      </NumberPadSection>
    </MyLayout>
  );
}

export default Money;
