import styled from "styled-components";
import React, {useState} from 'react';
import {CategoryType} from "views/Money";

const Wrapper = styled.section`
  font-size: 24px;
  > ul{
    display:flex;
    > li {
      width: 50%; 
      text-align:center;
      padding: 16px 0;
      position:relative;
      &.selected::after{
        content: '';
        display:block; 
        height: 3px;
        background:#333;
        position:absolute;
        bottom:0;
        width: 100%;
        left: 0;
      }
    }
  }
`;

type Props = { value: CategoryType, onChange: (value: CategoryType) => void }

const CategorySection: React.FunctionComponent<Props> = (props) => {
  const [categoryList] = useState<('-' | '+')[]>(['-', '+']);
  const category = props.value;
  const categoryMap = {'-': '支出', '+': '收入'};
  return (
    <Wrapper>
      <ul>
        {categoryList.map(c =>
          <li className={category === c ? 'selected' : ''} key={c} onClick={() => {
            props.onChange(c);
          }}>{categoryMap[c]}</li>
        )}
      </ul>
    </Wrapper>
  );
};

export {CategorySection};
